body{
  display: flex;
  justify-content: center;
  align-items: center;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.AppButton {
  width: 30%;
  background-color: rgba(178, 214, 255, 0.407);
  margin-bottom: 40px;
  margin-top: 20px;
  color: rgba(0, 0, 0, 0.776);
}
.AppCards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
